.contact-section a {
  font-family: "Kavivanar";
  font-size: 1.2rem;
  text-decoration: none;
  color: #b06d81;
}
.contact-section a:hover {
  color: rgb(31, 30, 30);
}
.contact-section h1 {
  color: 1.4rem;
  font-size: 2rem;
  font-family: "Kavivanar";
  color: #b06d81;
}

