  text-decoration: none;
  border-radius: 30px;
  height: 40px;
  width: 40%;
  margin: auto;
  margin-bottom: 5%;
  margin-top: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-section a.btn.btn-primary:hover {
  transition: 0.2s;
  background: #ced3b4;
}

